import React from 'react';
import './Main.css';
import Section from '../Section/Section.js';

function Main() {

  return(
    <Section title='' options={[]} heightType='page' headerType='large'>
      <div className='main'>
        <h1 className='main__title'>     </h1>
        <p className='main__subtitle'>    :</p>
        <div className='main__list'>
          <div className='main__item'>
            <p className='main__item-text'>  ( )</p>
          </div>
          <div className='main__item'>
            <p className='main__item-text'>  </p>
          </div>
        </div>
      </div>
    </Section>
  )
}

export default Main;